.wrapper {
  min-height: 100vh;
  display: grid;
  grid-template:
    minmax(auto, 30px) 1fr minmax(auto, 40px) / 1fr minmax(auto, 1400px)
    1fr;
  background-image: url(./images/back.jpg);
  color: white;

  font-family: "CENTURY GOTHIC";
}

.header {
  grid-row: 1/2;
  grid-column: 2/3;
  padding: 5px;
  background-image: url(./images/back.jpg);
  opacity: 0.7;
  /* box-shadow: 2px 4px 8px 0 #cacaca; */

  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 20;
}

.content {
  grid-row: 2/3;
  padding: 20px 15px;
  grid-column: 2/3;
}

.footer {
  grid-row: 3/4;
  grid-column: 2/3;
  display: flex;
  align-items: center;
}

a {
  text-decoration: none;
  color: white;
}

@media (max-width: 768px) {
  .wrapper {
    min-height: 100vh;
    display: grid;
    grid-template:
      minmax(auto, 10px) 1fr minmax(auto, 10px) / 1fr minmax(auto, 768px)
      1fr;
    background-image: url(./images/back.jpg);
    color: white;
  
    font-family: "CENTURY GOTHIC";
  }
  
  .header {
    grid-row: 1/2;
    grid-column: 2/3;
    background-image: url(./images/back.jpg);
    opacity: 0.7;
    /* box-shadow: 2px 4px 8px 0 #cacaca; */
  
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 20;
  }
  
  .content {
    grid-row: 2/3;
    grid-column: 2/3;
  }
  
  .footer {
    grid-row: 3/4;
    grid-column: 2/3;
    display: flex;
    align-items: center;
  }
  
  a {
    text-decoration: none;
    color: white;
  }
}